import React, { useState } from 'react';

export const ModalManagerStateContext = React.createContext({});

export const ModalManagerSetContext = React.createContext<
  React.Dispatch<React.SetStateAction<{}>>
>(() => {});

interface Props {
  defaultModals?: Record<string, any>;
}

export const ModalManagerProvider: React.FunctionComponent<Props> = ({
  children,
  defaultModals = {},
}) => {
  const [modals, setModals] = useState<Record<string, any>>(defaultModals);
  return (
    <ModalManagerStateContext.Provider value={modals}>
      <ModalManagerSetContext.Provider value={setModals}>
        {children}
      </ModalManagerSetContext.Provider>
    </ModalManagerStateContext.Provider>
  );
};
