import {
  ModalManagerStateContext,
  ModalManagerSetContext,
} from 'contexts/ModalManagerContext/ModalManagerContext';
import { useContext, useCallback } from 'react';

const useModalManager = <T extends any>(key: string) => {
  type ModalData = T | null | undefined;
  const modals = useContext<Record<string, ModalData>>(
    ModalManagerStateContext
  );

  const setModals = useContext(ModalManagerSetContext);

  const openModal = useCallback(
    (data: ModalData = {} as T) => {
      setModals((modals) => ({ ...modals, [key]: data }));
    },
    [key, setModals]
  );

  const closeModal = useCallback(() => {
    openModal(null);
  }, [openModal]);

  const modalData = modals[key];

  return {
    modalData: modalData as T,
    openModal,
    closeModal,
  };
};

export default useModalManager;
