import React from 'react';
import { OutlineButton } from '@cko/primitives';
import { IconActionInfo } from '@cko/icons';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import 'styled-components/macro';

interface Props {
  modalKey: Modals;
  buttonText?: string;
}

const OpenInfoModalButton: React.FunctionComponent<Props> = ({
  modalKey,
  buttonText = 'Learn more',
}) => {
  const { openModal } = useModalManager(modalKey);

  return (
    <OutlineButton onClick={() => openModal()}>
      <IconActionInfo
        css={`
          margin-right: 6px;
        `}
      />
      {buttonText}
    </OutlineButton>
  );
};

export default OpenInfoModalButton;
