import React from 'react';
import useModalManager from 'hooks/useModalManager';
import { Modals } from 'types/enums/modals';
import { Modal } from '@cko/primitives';

interface Props {
  modalKey: Modals;
  title: string;
}

const InfoModal: React.FunctionComponent<Props> = ({
  modalKey,
  title,
  children,
}) => {
  const { modalData, closeModal } = useModalManager(modalKey);

  if (!modalData) {
    return null;
  }

  return (
    <Modal size="large" type="success" title={title} closeModal={closeModal}>
      {children}
    </Modal>
  );
};

export default InfoModal;
